import { Field, reduxForm } from 'redux-form'
import cl from './Login.module.css'
import { CreateField, Input } from '../common/FormsControls/FormsControls'
import { requiredField } from '../../utils/validators/validators'
import { connect } from 'react-redux'
import { login } from '../../redux/authReducer'
import { Navigate } from 'react-router-dom'

let LoginForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit} className="" >
            <div className="itemForm">
                {//CreateField('email','email',[requiredField], Input)
                }
                <Field type="text" className="" name={'email'} placeholder={'email'} 
                    validate={[requiredField]}component={Input}/>
            </div>
            <div className="itemForm">
                <Field type="text" className="" name={'password'} placeholder={'password'} 
                     validate={[requiredField]} component={Input}/>
            </div>
            <div className="itemForm">
                <Field type="checkbox" className={cl.inlineBlock} name={'rememberMe'}  component={Input}/> remember me
            </div>
            {props.error && <div className={cl.form_summary_error}>{props.error}</div>}
            <div className="itemForm">
                <button>Login</button>
            </div>
        </form>
    )
}
LoginForm = reduxForm({ form:'login' })(LoginForm)


export const Login = (props) => {
    const onSubmit =(formData)=>{
        props.login(formData.email, formData.password, formData.rememberMe)
        //location.reload()
    }
    if(props.isAuth){
        return <Navigate to={'/profile'}/>
    }
    return (
        <div className={cl.body}>
            <h2 className="title">Login</h2>
            <LoginForm onSubmit={onSubmit} />
        </div>
    )
}
const mapStateToProps =(state)=>({isAuth: state.auth.isAuth})
export default connect(mapStateToProps, {login})(Login)



