import { useState } from 'react'
import cl from './Paginator.module.css'
export const Paginator = (props) => {
    const portionSize = 10
    let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize)
    let pages = []
    for (let i = 1; (i <= pagesCount); i++)
        pages.push(i)
    let portionCount = Math.ceil(pagesCount / portionSize)
    let [portionNamber, setPortionNumber] = useState(1)
    let leftportionPageNamber = (portionNamber - 1) * portionSize + 1
    let rightportionPageNamber = portionNamber * portionSize
    return (
        <div className={cl.listOfNumbers}>
            {portionNamber>1 &&
            <button onClick={() => { setPortionNumber(portionNamber - 1) }}>Prev</button>}
            {pages
                .filter(p => p >= leftportionPageNamber && p <= rightportionPageNamber)
                .map(p => {
                    return <span key={p}
                        className={`${props.currentPage === p ? cl.selectedPage : ''} ${cl.numPage}`}
                        onClick={(e) => { props.onPaheChanged(p) }}>
                        {p}</span>
                })}
            {portionCount>portionNamber &&
            <button onClick={() => { setPortionNumber(portionNamber + 1) }}>Next</button>}
        </div>
    )
}