import cl from './FormsControls.module.css' 
import { Field } from 'redux-form'

export const Textarea = ({input, meta, ...props}) =>{
    const showError = meta.touched && meta.error
    return(
        <div className={cl.form_control + ' ' + (showError?cl.error:'')}>
            <textarea {...input} {...props} className={cl.mainText}></textarea>
            { showError && <span>{meta.error}</span>}
        </div>
    )
}

export const Input = ({input, meta, ...props}) =>{
    const showError = meta.touched && meta.error
    return(
        <div className={cl.form_control + ' ' + (showError?cl.error:'')}>
            <input {...input} {...props} className={cl.mainText}/>
            { showError && <span>{meta.error}</span>}
        </div>
    )
}

export const CreateField = (placeholder, name, validators, component)=>{
    return (
        <Field type="text" className="" name={name} placeholder={placeholder} 
                    validate={validators}component={component}/>
    )
}