import { userApi } from "../api/api"
import { updateObjectInArray } from "../utils/objectsHelper"
const FOLLOW = 'FOLLOW'
const SET_USERS = 'SET_USERS'
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE'
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT'
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING'
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS'

let initialState = {
   users:[],
   pageSize: 4,
   totalUsersCount:0,
   currentPage:1,
   isFetching:false,
   followingInProgress:[]
}

export const usersReducer = (state = initialState, action) =>{
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                // users: state.users.map(user => {
                //     if (user.id === action.userId)
                //         return{...user, followed:!user.followed}
                //     return user
                // })
                users: updateObjectInArray(state.users, action.userId,'id',{followed:true})
            }
        case SET_USERS:
            return {
                    ...state,
                    users:action.users
                }
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage:action.currentPage
            }
        case SET_TOTAL_USERS_COUNT:
            return {
                ...state,
                totalUsersCount: action.totalUsersCount
            }
        case TOGGLE_IS_FETCHING:
            return {
                ...state,
                isFetching: action.isFetching
            }
         case TOGGLE_IS_FOLLOWING_PROGRESS:
            return {
                ...state,
                followingInProgress: action.isFetching
                                    ? [...state.followingInProgress, action.id]
                                    : state.followingInProgress.filter(id=>id!==action.id)
            }
        default:
            return state
    }
}

export const FollowSuccess = (userId) => { return { type: FOLLOW, userId}}
export const setUsers = (users) => { return { type: SET_USERS, users}}
export const setCurrentPage = (currentPage) => { return { type: SET_CURRENT_PAGE, currentPage}}
export const setTotalUsersCount = (totalUsersCount) => { return { type: SET_TOTAL_USERS_COUNT, totalUsersCount}}
export const setIsFetching = (isFetching) => { return { type: TOGGLE_IS_FETCHING, isFetching}}
export const setFollowing = (isFetching, id) => { return { type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, id}}

export const getUsersThunkCreator = (pageNumber, pageSize) => {
    return (dispatch) => {
        dispatch(setIsFetching(true))
        dispatch(setCurrentPage(pageNumber))
        userApi.getUsers(pageNumber, pageSize).then(data => {
            dispatch(setIsFetching(false))
            dispatch(setUsers(data.items))
            dispatch(setTotalUsersCount(data.totalCount))
        })
    }
}
export const follow = (userId) => {
    return (dispatch) => {
        dispatch(setFollowing(true, userId))
        userApi.follow(userId).then(data => {
            if (data.resultCode === 0)
                dispatch(FollowSuccess(userId))
            dispatch(setFollowing(false, userId))
        })
    }
}
export const unfollow = (userId) => {
    return (dispatch) => {
        dispatch(setFollowing(true, userId))
        userApi.unfollow(userId).then(data => {
            if (data.resultCode === 0)
                dispatch(FollowSuccess(userId))
            dispatch(setFollowing(false, userId))
        })
    }
}
