import { NavLink } from 'react-router-dom';
import cl from './Navigation.module.css'
const Navigation = () => {
    return (
        <nav className={cl.menu}>
        <ul className={cl.menu__body}>
          <li className={cl.item}>
            <NavLink to="/profile" className = { n => n.isActive ? cl.active : "" }>Profile</NavLink>
          </li>
          <li className={cl.item}>
            <NavLink to="/dialogs" className = { n => n.isActive ? cl.active : "" }>Messages</NavLink>
          </li>
          <li className={cl.item}>News</li>
          <li className={cl.item}>Music</li>
          <li className={cl.item}>Settings</li>
          <li className={cl.item}>
            <NavLink to="/users" className = { n => n.isActive ? cl.active : "" }>Users</NavLink>
          </li>
        </ul>
      </nav>
    );
  }
export default Navigation;
  