const SEND_MESSAGE = 'SEND_MESSAGE'

let initialState = {
    dialogData: [
        { id: 0, name: 'NameMain' },
        { id: 1, name: 'Name2' },
        { id: 2, name: 'Name3' },
        { id: 3, name: 'Name10' },
        { id: 4, name: 'NNN' }
    ],
    messagesData: [
        { id: 0, message: 'Newmessage1' },
        { id: 1, message: 'message2' },
        { id: 2, message: 'message3' },
        { id: 3, message: 'message4' },
        { id: 4, message: 'message5' }
    ],
}

export const dialogsReducer = (state = initialState, action) =>{
    switch (action.type) {
        case SEND_MESSAGE:
            return {
                ...state,
                messagesData:[...state.messagesData, { id: 5, message: action.newMessageBody }],
            }
        default:
            return state
    }
}

export const SendMessageCreator = (newMessageBody) => {return { type: SEND_MESSAGE, newMessageBody}}
