import cl from './Post.module.css'

const Posts = (props) => {
  return (
    <div className={cl.item}>
      <div className="">
        <img src="https://i.cbc.ca/1.5359228.1577206958!/fileImage/httpImage/image.jpg_gen/derivatives/16x9_940/smudge-the-viral-cat.jpg" alt="" />
        {props.message}
      </div>
      <div className={cl.btnLike}>Like</div>
    </div>

  );
}

export default Posts;
