import { profileApi, userApi } from "../api/api"
const ADD_POST = 'ADD-POST'
const SET_USER_PROFILE = 'SET_USER_PROFILE'
const SET_STATUS = 'SET_STATUS'

let initialState = {
    postData: [
        { id: 0, message: 'PostNumber 1' },
        { id: 1, message: 'post2 Super66' },
        { id: 2, message: 'post3' },
    ],
    profile:null,
    status: ''
}

export const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            return {
                ...state,
                postData: [...state.postData, { id: 3, message: action.newPostText }],
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile:action.profile
            }
        case SET_STATUS:
            return {
                ...state,
                status: action.status
            }
        default:
            return state
    }
}

export const addPostActionCreator = (newPostText) => { return { type: ADD_POST, newPostText}}
export const setUserProfile = (profile) => { return { type: SET_USER_PROFILE, profile }}
export const setStatus = (status) => { return { type: SET_STATUS, status }}

export const getUserThuncCreator = (userId) => {
    return (dispatch)=>{
        userApi.getProfile(userId).then(data =>{ 
            dispatch(setUserProfile(data))
        })
    }
}
export const getStatusThuncCreator = (userId) => {
    return (dispatch)=>{
        profileApi.getStatus(userId).then(data =>{ 
            dispatch(setStatus(data))
        })
    }
}
export const updateStatusThuncCreator = (status) => {
    return (dispatch)=>{
        profileApi.updateStatus(status).then(data =>{ 
            if(data.resultCode === 0)
                dispatch(setStatus(status))
        })
    }
}