import Preloader from '../../common/preloader/preloader';
import cl from './ProfileInfo.module.css'
///import { ProfileStatus } from './ProfileStatus';
import { ProfileStatusWithHooks } from './ProfileStatusWithHooks';

const defaultBack ='https://as2.ftcdn.net/v2/jpg/03/53/58/95/1000_F_353589554_ohwrAkwigqCqloRppiMh4zfMXNAshMWM.jpg'
const defaultAvatar ='https://img.favpng.com/21/4/9/portable-network-graphics-avatar-computer-icons-image-social-media-png-favpng-r3ez8qWcYdM8jGVn2b5TGhvS8.jpg'
const ProfileInfo = (props) => {
    return (!props.profile ? <Preloader /> :
      <div>
        <div className={`${cl.img} img`}>
          <img src={defaultBack} alt="background" />
        </div>
        <div className={cl.profile}>
          <div className={`${cl.imgAvatar} img`}> 
            <img src={props.profile.photos.large===null?defaultAvatar:props.profile.photos.large} alt="avatar" /> 
          </div>
          <div className="desc">
            <div className="">{props.profile.fullName}</div>
            <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
            
          </div>
        </div>
      </div>
    )
}

export default ProfileInfo;
