import cl from './Profile.module.css'
import React from 'react'
import Profile from './Profile';
import { connect } from 'react-redux';
import {getUserThuncCreator, getStatusThuncCreator, updateStatusThuncCreator } from '../../redux/profileReducer';
import { whithAuthRedirect } from '../../hoc/authRedirect';
import { compose } from 'redux';
import { withRouter } from '../../hoc/withRouter';

class ProfileContainer extends React.Component {
    componentDidMount(){
        let userId = this.props.router.params.userId
        if(userId === undefined){
            userId = this.props.userId
            if(!userId){
                userId = 2
            }
        }
        this.props.getUserThuncCreator(userId)
        this.props.getStatusThuncCreator(userId)
    }
    render() {
        return (
            <div className={cl.profile}>
                <Profile {...this.props} profile={this.props.profile}/>
            </div>
        )
    }
}

let mapStateToProps = (state)=>({
    profile: state.profilPage.profile,
    status: state.profilPage.status,
    userId: state.auth.id,
    isAuth: state.auth.isAuth
})
export default compose( connect(mapStateToProps, {getUserThuncCreator, getStatusThuncCreator, updateStatusThuncCreator}),
                        withRouter,
                        //whithAuthRedirect
                    )(ProfileContainer)