import { NavLink } from 'react-router-dom';
import cl from './Users.module.css'
const ava = 'https://thumbs.dreamstime.com/b/real-people-portraits-male-faces-shoulders-avatars-flat-style-vector-icons-set-modern-beautiful-avatar-man-real-people-218407465.jpg'
export const User = ({u, unfollow, follow, followingInProgress}) => {
    return (
        <div key={u.id} className={cl.userList__item}>
            <div className={cl.avatar}>
                <div className={cl.img}>
                    <NavLink to={'/profile/' + u.id}>
                        <img src={u.photos.small !== null ? u.photos.small : ava} alt="avatar" />
                    </NavLink>
                </div>
                <div className={cl.follow}><button disabled={followingInProgress.some(id => id === u.id)}
                    onClick={() => { u.followed ? unfollow(u.id) : follow(u.id) }}
                >{u.followed ? 'unfollow' : 'follow'}</button></div>
            </div>
            <div className={cl.descContainer}>
                <div className={cl.descr}>
                    <div className={cl.left}>
                        <div className="name">{u.name}</div>
                        <div className="status">{u.status}</div>
                    </div>
                    <div className={cl.right}>
                        <div className="country">{'u.location.country'}</div>
                        <div className="city">{'u.location.city'}</div>
                    </div>
                </div>
            </div>
        </div>
    )
}