import UsersApiComponent from './UsersApiComponent'
import { follow, unfollow , setCurrentPage, getUsersThunkCreator} from '../../redux/UsersReducer'
import { connect } from 'react-redux'
import { whithAuthRedirect } from '../../hoc/authRedirect'
import { compose } from 'redux'
import { getUsersPage } from '../../redux/selectors/usersSelectors'

// const mapStateToProps = (state) => { 
//     return { 
//         state: state.usersPage,
//     } 
// }
const mapStateToProps = (state) => { 
    return { 
        state: getUsersPage(state)
    } 
}
export default compose(
        connect(mapStateToProps, {follow, setCurrentPage, unfollow, getUsersThunkCreator }),
       // whithAuthRedirect
    )(UsersApiComponent)
