import { Paginator } from '../common/paginator/Paginator';
import { User } from './User';
import cl from './Users.module.css'


let Users = (props) => {
    return (
        <div className={cl.users}>
            <h3 className={cl.title}>Users</h3>
            <Paginator totalUsersCount={props.state.totalUsersCount} 
                        pageSize={props.state.pageSize} 
                        currentPage={props.state.currentPage} onPaheChanged={props.onPaheChanged}/>
            <div className="usersList">
                {props.state.users.map(u => {
                    return (
                        <User u={u} key={u.id}
                            unfollow={props.unfollow} follow={props.follow} 
                            followingInProgress={props.state.followingInProgress}/>
                    )
                }
                )}
            </div>
        </div>
    );
}

export default Users