import React from 'react'
import Users from './Users'
import Preloader from '../common/preloader/preloader'


class UsersApiComponent extends React.Component {
    componentDidMount(){
        this.props.getUsersThunkCreator(this.props.state.currentPage, this.props.state.pageSize)
    }
    onPaheChanged = (pageNumber)=>{
        this.props.setCurrentPage(pageNumber)
        this.props.getUsersThunkCreator(pageNumber, this.props.state.pageSize)
    }
    render(){
        return <>
        {this.props.state.isFetching
                ? <Preloader/> 
                :<Users state={this.props.state} 
                        onPaheChanged={this.onPaheChanged} 
                        follow={this.props.follow}
                        unfollow={this.props.unfollow}/>}
        </>
        
       
    }
}
export default UsersApiComponent;
