import React from 'react';
import cl from './MyPost.module.css'
import Posts from './Posts/Post';
import { Field, reduxForm } from 'redux-form';
import { maxLength, requiredField } from '../../../utils/validators/validators';
import { Textarea } from '../../common/FormsControls/FormsControls';

const MyPosts = React.memo((props) => {
  let onSubmitForm = (values) => { props.addPost(values.newPostText) }
  return (
    <div className={cl.main__posts}>
      posts
      <div className={cl.newPost}>
        <AddNewPostFormRedux onSubmit={onSubmitForm} />
      </div>
      <div className={cl.postList}>
        {props.state.postData.map(el => (<Posts message={el.message} key={el.id} />))}
      </div>
    </div>
  );
})

let maxLength10 = maxLength(10)
const addNewPostForm =(props)=>{
  return(
    <form onSubmit={props.handleSubmit} className="">
        <Field 
                component={Textarea} name='newPostText' 
                placeholder='input message to post'
                validate={[requiredField,maxLength10 ]}/>
        <button>Add post</button>
    </form>
  )
}
const AddNewPostFormRedux = reduxForm({form:'ProfileAddNewPost'})(addNewPostForm)
export default MyPosts;
